<script>
    import { LayerCake, Svg, Html } from 'layercake';
    import { feature } from 'topojson-client';
    import { geoMercator } from 'd3-geo';
    import { scaleQuantize } from 'd3-scale';
  
    import MapSvg from './components/Map.svg.svelte';
    import Tooltip from './components/Tooltip.svelte';
  
    // This example loads json data as json using @rollup/plugin-json
    import btwMap from './data/geo.topo.json';
    import btwData from './data/btw17_constituency.json';
    import btwData21 from './data/btw21_pred2.tsv';

    export let colorDictionary;
    export let selectedYear = "17";
    export let selectedRegionData;
    export let showAll;
    /* --------------------------------------------
     * Create lookups to more easily join our data
     */
    const joinKey = 'name';
    const dataLookup = new Map();
  
    const geojson = feature(btwMap, btwMap.objects.constituencies);
    const projection = geoMercator();

    $: selectedYear, selectedRegionData, geojson.features = geojson.features.map(d => {
      // This will overwrite any existing keys on d.properties
      // so watch out for any name collision
      let fill = "#ccc";

      if(selectedYear === "17") {
        if(btwData.find(e => +e.id === +d.id)) {
          fill = colorDictionary[btwData.find(e => +e.id === +d.id).party];
        }
      }
      if(selectedYear === "21") {
        if(btwData21.find(e => +e.id === +d.id)) {
          fill = colorDictionary[btwData21.find(e => +e.id === +d.id).party];
        }
      }

      let fillOpacity = 1;
      
      if(!showAll && selectedRegionData && selectedRegionData.seats.map(d => d.id).indexOf(d.id) === -1) {
        fillOpacity = 0.5;
      }

      return {
        ...d,
        fill,
        fillOpacity
      };
    });

    let evt;
    let hideTooltip = true;
  
    // Create a flat array of objects that LayerCake can use to measure
    // extents for the color scale
    const flatData = geojson.features.map(d => d.properties);
    const colors = ['#ffdecc', '#ffc09c', '#ffa06b', '#ff7a33'];
  </script>
  
  <style>
    /*
      The wrapper div needs to have an explicit width and height in CSS.
      It can also be a flexbox child or CSS grid element.
      The point being it needs dimensions since the <LayerCake> element will
      expand to fill it.
    */
    .chart-container {
      width: 100%;
      height: 100%;
    }
  </style>
  
  <div class="chart-container">
    <LayerCake
      data={geojson}
      zScale={scaleQuantize()}
      zRange={colors}
      {flatData}
    >
      <Svg>
        <MapSvg
          {projection}
          on:mousemove={event => evt = hideTooltip= event}
          on:mouseout={() => hideTooltip = true}
          features={geojson.features}
        />
      </Svg>
    </LayerCake>
  </div>
  
  