
function calculateRound(divisor, region) {
    let seats = region.pr;
    let parties = Object.keys(region.regionInteractiveShare);

    let allocated = 0;
    let obj = {};

    for(let i = 0; i < parties.length; i++) {
      if(parties[i] !== "Oth") {
        let share = +region.regionInteractiveShare[parties[i]];
        let pr = Math.round(share / divisor);
        let fptp = region.fptpSeats[parties[i]];
        let ave = Math.round((pr + fptp) / 2);
        obj[parties[i]] = {
          pr,
          fptp,
          overhang: Math.max(0, fptp - pr), 
          ave,
          max: Math.max(ave, fptp),
        };

        allocated += pr;
      }
    }

    if(allocated > seats) {
      return calculateRound(divisor + 0.01, region);
    }
    else {
      return obj;
    }

}

function calculatePrSeats(region){
    let arr = {};

    if(region.regionInteractiveShare) {
      let seats = region.pr;

      // basically fudge this number till it totals 598?
      let divisor = 88.5 / seats;
      
      arr = calculateRound(divisor, region)
    }

    return arr; 
}

function calcRoundRd2(data, seats, isRepeat) {
  let { total, parties, prevRound } = data;

  let overhang = 0;
  let divisor = total / seats;
  let output = {};

  for(let i = 0; i < parties.length; i++) {
    if(prevRound.find(d => d.party === parties[i])) {
      let { votes, max } = prevRound.find(d => d.party === parties[i]);
      let pr = Math.round(+votes / divisor);
  
      output[parties[i]] = pr;
      overhang += Math.max(0, (max - pr));
    }
  }

  if(overhang > 3 && !isRepeat) {
    return calcRoundRd2(data, seats + 1);
  }
  else {
    return output;
  }
}

function calculatePrSeatsRd2(data) {
  let seats = 598;
  let output = calcRoundRd2(data, seats);
  return output;
}

function getVotes(region, selectedParty) {
  let shareKeys = Object.keys(region.regionInteractiveShare);
  let total = shareKeys.map(d => region.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);

  return region.turnout * (region.regionInteractiveShare[selectedParty] / total);
}

function calculatePrSeatsRd3(resultRd2, regions, parties) {

  let output = {};

  for(let i = 0; i < parties.length; i++) {
    let partyTotal = Object.values(regions).reduce((prev, curr) => {
      return prev + getVotes(curr, parties[i]);
    }, 0)


    Object.keys(regions).map(d => {
      let divisor = partyTotal / resultRd2[parties[i]];
      
      let prevMin = regions[d].calculatedSeats[parties[i]].max;
      let votes = getVotes(regions[d], parties[i]);
      let newPr = Math.round(+votes / divisor);

      output[d] = output[d] ? output[d] : {};

      output[d][parties[i]] = {
        share: votes,
        newPr,
        prevMin,
        fptp: regions[d].fptpSeats[parties[i]],
        finalResult: Math.max(newPr, prevMin),
      };
    });
  }

  return output;

}

export { calculatePrSeats, calculatePrSeatsRd2, calculatePrSeatsRd3 };