<script>
    import { regionsData } from './regionStore.js';
    import { calculatePrSeats } from './calculatePr';

    export let region;
    export let split;
    export let colorDictionary;
    export let calculated;
    export let selectedYear = "21";

    let partyNames = ["CDU","SPD","AfD","FDP","LINKE","Grn"];
    let parties = partyNames;
  
    function changeParty(ward) {
      let {party, prevParty} = ward;

      let newParty = parties[parties.indexOf(party) + 1];
      
      if(!newParty) {
        newParty = parties[0];
      }

      if(!prevParty) {
        prevParty = party;
      }
      
      if(!region.wardResults[newParty]) {
        region.wardResults[newParty] = 0;
      }
      if(!region.wardResults[prevParty]) {
        region.wardResults[prevParty] = 0;
      }

      region.wardResults[newParty] = region.wardResults[newParty] + 1;
      region.wardResults[prevParty] = region.wardResults[prevParty] - 1;
      ward.prevParty = party;
      ward.party = newParty;
    }

    let shareKeys = Object.keys(region.regionInteractiveShare);

    let total = shareKeys.map(d => region.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
    
    function updateStore() {
      total = shareKeys.map(d => region.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
     
      // TODO: update store;
      $regionsData[region.name].regionInteractiveShare = region.regionInteractiveShare;

      $regionsData[region.name].calculatedSeats = calculatePrSeats(region);
    }

    $: selectedYear = selectedYear;
</script>

<div class="region">
    <h5>{region.name} - {region.fptpTotal} constituencies + {region.listTotal} land/list</h5>

    <div class="seats-grid">
      <div>
        <p>{selectedYear === "21" ? "2021 INSA forecast" : "2017 constituency results"}:<br>{region.fptpTotal}</p>
        {#each region.wardResults as d}
          <span class="cir" style="background: {colorDictionary[d.party]}" on:click={() => changeParty(d)}></span>
        {/each}
        {#if selectedYear === "17"}
        <div>
          <p>2017 list seats: {region.listTotal}</p>
          {#each Object.keys(region.listSeats) as d}
            {#each (new Array(region.listSeats[d]).fill(0)) as e}
              <span class="cir" style="background: {colorDictionary[d]}"></span>
            {/each}
          {/each}
        </div>
        {/if}
      </div>

        {#if calculated}
        <div>
          <p>Calculated, first allocation<br>Step A: {Object.values(region.calculatedSeats).reduce((p, c) => p + c.pr, 0)}</p>
          {#each Object.keys(region.calculatedSeats) as party}
            {#each new Array(region.calculatedSeats[party].pr).fill(0) as d}
              <span class="cir fade" style="background: {colorDictionary[party]}"></span>
            {/each}
          {/each}
        </div>
        
        <div>
          <p>Calculated, final allocation<br>Step C: {Object.values(calculated[region.name]).reduce((p, c) => p + c.finalResult, 0) - region.fptpTotal}</p>
          {#each Object.keys(calculated[region.name]) as party}
            {#each new Array(calculated[region.name][party].finalResult - calculated[region.name][party].fptp).fill(0) as d}
              <span class="cir" style="background: {colorDictionary[party]}"></span>
            {/each}
          {/each}
        </div>
        {/if}
      </div>
      {#if calculated}
      <div class="sliders-wrap">
        <!-- <h5>Change the party list result to re-calculate the list seats:</h5> -->
        <h5>Calculated list party results (edit using the national swing to the left)</h5>
        <div class="sliders seats-grid">
          {#each parties as party}
            <div class="slider-{party.toLowerCase()}">
              <p>
                {partyNames[parties.indexOf(party)]} - {(region.regionInteractiveShare[party] / total * 100).toFixed(1) || 0}%
              </p>
              <input disabled type="range" min="0" max="100" bind:value={region.regionInteractiveShare[party]} on:change={updateStore} on:blur={updateStore} />
            </div>
          {/each}
        </div>
      </div>
      {/if}
  </div>

  <style>
    .sliders-wrap {
      margin-top: 1em;
    }
    .seats-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    }

    h5 {
      font-size: 1em;
      margin: 0;
    }

    .ward {
      cursor: pointer;
    }

    input {
      position: relative;
      margin-right: 8px;
    }

    .sliders {
      margin-top: 1em;
      /* display: grid;
      grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); */
    }

    .sliders div {
      min-height: 60px;
      padding-left: 8px;
    }

    .sliders input {
      border: 0;
      width: 90%;
    }

    .sliders input[type="range"]::-webkit-slider-runnable-track {
      background: #ccc;
      height: 2px;
    }

    .sliders input[type="range"]::-moz-range-track {
      background: #ccc;
      height: 2px;
    }

    .sliders input[type="range"]::-webkit-slider-thumb {
      height: 20px;
      width: 20px;
      border-radius: 60px;
    }

    .sliders input[type="range"]::-moz-range-thumb {
      height: 20px;
      width: 20px;
      border-radius: 60px;
    }


    .slider-cdu input[type="range"]::-webkit-slider-thumb,
    .slider-cdu input[type="range"]::-moz-range-thumb {
      background: #2E2E2E;
    }
    .slider-spd input[type="range"]::-webkit-slider-thumb,
    .slider-spd input[type="range"]::-moz-range-thumb {
      background: #EE493A;
    }
    .slider-afd input[type="range"]::-webkit-slider-thumb,
    .slider-afd input[type="range"]::-moz-range-thumb {
      background: #6FC2E3;
    }
    .slider-fdp input[type="range"]::-webkit-slider-thumb,
    .slider-fdp input[type="range"]::-moz-range-thumb {
      background: #FFCA52
    }
    .slider-linke input[type="range"]::-webkit-slider-thumb,
    .slider-linke input[type="range"]::-moz-range-thumb {
      background: #B84667;
    }
    .slider-grn input[type="range"]::-webkit-slider-thumb,
    .slider-grn input[type="range"]::-moz-range-thumb {
      background: #62C258;
    }
    .slider-oth input[type="range"]::-webkit-slider-thumb,
    .slider-oth input[type="range"]::-moz-range-thumb {
      background: #ccc;
    }

    .region {
      padding: 2em 0;
    }

    .cir { display: inline-block; width: 16px; height: 16px; margin: 0 2px; background: #ccc; border-radius: 20px; }

    .fade {
      opacity: 0.5;
    }

    .num { text-align:right; }

  </style>
