<script>
	import { LayerCake, Svg, uniques } from 'layercake';
	import { tsvParse } from 'd3-dsv';
	import MapSvg from './components/Map.svg.svelte';
	import Region from './Region.svelte';
	import Map from './Map.svelte';
	import Section from './Section.svelte';
	import HexMap from './data/tiles.topo.json';
	import HexData from './data/btw17_constituency.json';
	import Pred21 from './data/btw21_pred2.tsv';
	import { calculatePrSeats, calculatePrSeatsRd2, calculatePrSeatsRd3 } from './calculatePr';

	import { regionsData } from './regionStore.js';
	
	let selectedYear = "17";
	
	let partyListResults = tsvParse(`region	CDU	SPD	AfD	FDP	LINKE	Grn	Oth	pr	turnout
Schleswig-Holstein	34.0	23.3	8.2	12.6	7.3	12.0	2.7	22	16.7
Mecklenburg-Vorpommern	33.1	15.1	18.6	6.2	17.8	4.3	4.9	13	8.82
Hamburg	27.2	23.5	7.8	10.8	12.2	13.9	4.5	12	9.34
Lower Saxony	34.9	27.4	9.1	9.3	6.9	8.7	3.6	59	44.8
Bremen	25.0	26.3	10.0	9.3	13.5	11.0	4.3	5	3.18
Brandenburg	26.7	17.6	20.2	7.1	17.2	5.0	6.3	20	14.0
Saxony-Anhalt	30.3	15.2	19.6	7.8	17.8	3.7	5.7	17	11.7
Berlin	22.7	17.9	12.0	8.9	18.8	12.6	7.0	24	17.4
North Rhine-Westphalia	32.6	26.0	9.4	13.1	7.5	7.6	3.8	128	94.8
Saxony	26.9	10.5	27.0	8.2	16.1	4.6	6.7	32	23.1
Hesse	30.9	23.5	11.9	11.6	8.1	9.7	4.4	43	32.0
Thuringia	28.8	13.2	22.7	7.8	16.9	4.1	6.5	17	12.1
Rhineland-Palatinate	35.9	24.2	11.2	10.4	6.8	7.6	3.9	30	22.7
Bavaria	38.8	15.3	12.4	10.2	6.1	9.8	7.5	93	68.4
Baden-Württemberg	34.4	16.4	12.2	12.7	6.4	13.5	4.5	76	57.2
Saarland	32.4	27.2	10.1	7.6	12.9	6.0	3.9	7	5.62`);
  
	let regionData = tsvParse(`region	LINKE	SPD	GRN	FDP	CDU	AfD	total	constituency
Schleswig-Holstein	2	6	3	3	10	2	26	11
Mecklenburg-Vorpommern	3	2	1	1	6	3	16	6
Hamburg	2	5	2	2	4	1	16	6
Lower Saxony	5	20	6	7	21	7	66	30
Bremen	1	2	1		1	1	6	2
Brandenburg	4	4	1	2	9	5	25	10
Saxony-Anhalt	4	3	1	2	9	4	23	9
Berlin	6	5	4	3	6	4	28	12
North Rhine-Westphalia	12	41	12	20	42	15	142	64
Saxony	6	4	2	3	12	11	38	16
Hesse	4	12	5	6	17	6	50	22
Thuringia	3	3	1	2	8	5	22	8
Rhineland-Palatinate	3	9	3	4	14	4	37	15
Bavaria	7	18	11	12	46	14	108	46
Baden-Württemberg	6	16	13	12	38	11	96	38
Saarland	1	3	1	1	3	1	10	4`);

	regionsData.init(regionData, HexData, partyListResults, Pred21, selectedYear === "21");
  
	let regionDataMapped = $regionsData;
  
	let selectedRegion = "Hesse";
  
	const colorDictionary = {
	  "CDU/CSU": "#2E2E2E",
	  "CDU": "#2E2E2E",
	  "CSU": "#2E2E2E",
	  "SPD": "#EE493A", 
	  "AfD": "#6FC2E3", 
	  "FDP": "#FFCA52", 
	  "Left": "#B84667",
	  "LINKE": "#B84667",
	  "LNK": "#B84667",
	  "Grn": "#62C258",
	  "Green": "#62C258",
	  "GRÜNE": "#62C258",
	}
  
	let partiesArr = ["CDU",	"SPD",	"AfD",	"FDP",	"LINKE",	"Grn"]; //,	"Oth"];

	let showRows = true;

	let selectedParty = "SPD";
	let selectedPartySwing = 0;
  
	function getVotes(region, selectedParty) {
	  let shareKeys = Object.keys(region.regionInteractiveShare);
	  let total = shareKeys.map(d => region.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
  
	  return region.turnout * (region.regionInteractiveShare[selectedParty] / total);
	}
  
	let regionKeys = Object.keys($regionsData);
  
	$: sliceStart = showRows ? 0 : Math.max(0, Object.keys(regionDataMapped).indexOf(selectedRegion) - 1);
	$: sliceEnd = showRows ? 20 : sliceStart + 3;

	$: selectedRegionData = regionDataMapped[selectedRegion];
  
	$: {
	  if(selectedRegionData) {
		selectedRegionData.partyListResult;
	  }
	}
  
	$: totals = Object.values($regionsData).reduce((prev, curr) => {
	  return {
		pr:       prev.pr + curr.calculatedSeats[selectedParty].pr,
		fptp:     prev.fptp + curr.calculatedSeats[selectedParty].fptp,
		overhang: prev.overhang + curr.calculatedSeats[selectedParty].overhang,
		ave:      prev.ave + curr.calculatedSeats[selectedParty].ave,
		max:      prev.max + curr.calculatedSeats[selectedParty].max,
		votes:    prev.votes + getVotes(curr, selectedParty),
	  };
	}, {
	  pr:0,
	  fptp:0,
	  overhang:0,
	  ave:0,
	  max: 0,
	  votes: 0
	})
  
	$: countryDistrubtion = partiesArr.map(party => {
	  let values =  Object.values($regionsData).reduce((prev, curr) => {
		
		let shareKeys = Object.keys(curr.regionInteractiveShare);
		let total = shareKeys.map(d => curr.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
  
		return curr.calculatedSeats[party] ? {
		  party,
		  pr:       prev.pr + curr.calculatedSeats[party].pr,
		  fptp:     prev.fptp + curr.calculatedSeats[party].fptp,
		  overhang: prev.overhang + curr.calculatedSeats[party].overhang,
		  ave:      prev.ave + curr.calculatedSeats[party].ave,
		  max:      prev.max + curr.calculatedSeats[party].max
		} : prev;
	  }, {
		pr:0,
		fptp:0,
		overhang:0,
		ave:0,
		max: 0,
		votes: 0,
	  });
  
	  values.max = Math.max(values.pr, values.max);
  
	  return values;
	});
  
	$: countryDistrubtionR2 = partiesArr.map(party => {
	  let values =  Object.values($regionsData).reduce((prev, curr) => {
  
		let shareKeys = Object.keys(curr.regionInteractiveShare);
		let total = shareKeys.map(d => curr.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
  
		return curr.calculatedSeats[party] ? {
		  party,
		  votes:    prev.votes + (curr.turnout * (curr.regionInteractiveShare[party] / total)),
		  fptp:     prev.fptp + curr.calculatedSeats[party].fptp,
		  overhang: prev.overhang + curr.calculatedSeats[party].overhang,
		  ave:      prev.ave + curr.calculatedSeats[party].ave,
		  max:      prev.max + curr.calculatedSeats[party].max,
		} : prev;
	  }, {
		votes:0,
		fptp:0,
		overhang:0,
		ave:0,
		max: 0,
	  });
  
	  values.votes = values.votes.toFixed(1);
  
	  return values;
	});
  
	$: countryTotalRd2 = countryDistrubtionR2.reduce((prev, curr) => {
	return {
		votes:       prev.votes + +curr.votes,
		max:     prev.max + curr.max,
		overhang: prev.overhang + curr.overhang,
	  };
	}, {
	  votes:0,
	  max:0,
	  overhang:0,
	})
  
	$: rd2Result = calculatePrSeatsRd2({ total: countryTotalRd2.votes, 
		parties: partiesArr, 
		prevRound: countryDistrubtionR2});
  
	$: regionDistrubtionR3 = calculatePrSeatsRd3(rd2Result, $regionsData, partiesArr);
  
	$: countryTotal = countryDistrubtion.reduce((prev, curr) => {
	  let max = Math.max(curr.max, curr.fptp);
	  return {
		pr:       prev.pr + curr.pr,
		fptp:     prev.fptp + curr.fptp,
		overhang: prev.overhang + curr.overhang,
		ave:      prev.ave + curr.ave,
		max:      prev.max + max,
	  };
	}, {
	  pr:0,
	  fptp:0,
	  overhang:0,
	  ave:0,
	  max: 0
	})
  
	$: r3Total = Object.values(regionDistrubtionR3).reduce((prev, curr) => {
		return {
			newPr: prev.newPr + curr[selectedParty].newPr,
			prevMin: prev.prevMin + curr[selectedParty].prevMin,
			finalResult: prev.finalResult + curr[selectedParty].finalResult,
		}
	}, {
		newPr: 0,
		prevMin: 0,
		finalResult: 0,
	});

	$: national = Object.values(regionDistrubtionR3).reduce((prev, curr) => {
		return {
			"CDU":  prev["CDU"] + curr["CDU"].finalResult,
			"SPD":  prev["SPD"] + curr["SPD"].finalResult,
			"AfD":  prev["AfD"] + curr["AfD"].finalResult,
			"FDP":  prev["FDP"] + curr["FDP"].finalResult,
			"LINKE": prev["LINKE"] + curr["LINKE"].finalResult,
			"Grn":  prev["Grn"] + curr["Grn"].finalResult,
		};
	}, {"CDU": 0,
		"SPD": 0,
		"AfD": 0,
		"FDP": 0,
		"LINKE": 0,
		"Grn": 0,
	});

	let showControls = true;

	let stepA = false;
	let stepB = false;
	let stepC = false;

	$: {
		selectedYear; 

		regionsData.init(regionData, HexData, partyListResults, Pred21, selectedYear === "21");

		for(let i = 0; i < regionKeys.length; i++) {

			for(let j = 0; j < partiesArr.length; j++) {
				let value = selectedPartySwing;

				if(partiesArr[j] !== selectedParty) {
					value = -1 * selectedPartySwing / 5;
				}

				let newValue = $regionsData[regionKeys[i]].regionInteractiveShare[partiesArr[j]] + value;

				// if(newValue < 5) {
				// 	newValue = 0;
				// }

				$regionsData[regionKeys[i]].regionInteractiveShare[partiesArr[j]] = newValue;
			}

			$regionsData[regionKeys[i]].regionInteractiveShare = 
			$regionsData[regionKeys[i]].regionInteractiveShare;
		}
	
		// setTimeout(() => {
		// 	selectedPartySwing = 0;
		// }, 500)
		
		totals = Object.values($regionsData).reduce((prev, curr) => {
			return {
				pr:       prev.pr + curr.calculatedSeats[selectedParty].pr,
				fptp:     prev.fptp + curr.calculatedSeats[selectedParty].fptp,
				overhang: prev.overhang + curr.calculatedSeats[selectedParty].overhang,
				ave:      prev.ave + curr.calculatedSeats[selectedParty].ave,
				max:      prev.max + curr.calculatedSeats[selectedParty].max,
				votes:    prev.votes + getVotes(curr, selectedParty),
			};
			}, {
			pr:0,
			fptp:0,
			overhang:0,
			ave:0,
			max: 0,
			votes: 0
			})
		
			countryDistrubtion = partiesArr.map(party => {
			let values =  Object.values($regionsData).reduce((prev, curr) => {
				
				let shareKeys = Object.keys(curr.regionInteractiveShare);
				let total = shareKeys.map(d => curr.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
		
				return curr.calculatedSeats[party] ? {
				party,
				pr:       prev.pr + curr.calculatedSeats[party].pr,
				fptp:     prev.fptp + curr.calculatedSeats[party].fptp,
				overhang: prev.overhang + curr.calculatedSeats[party].overhang,
				ave:      prev.ave + curr.calculatedSeats[party].ave,
				max:      prev.max + curr.calculatedSeats[party].max
				} : prev;
			}, {
				pr:0,
				fptp:0,
				overhang:0,
				ave:0,
				max: 0,
				votes: 0,
			});
		
			values.max = Math.max(values.pr, values.max);
		
			return values;
			});
		
			countryDistrubtionR2 = partiesArr.map(party => {
			let values =  Object.values($regionsData).reduce((prev, curr) => {
		
				let shareKeys = Object.keys(curr.regionInteractiveShare);
				let total = shareKeys.map(d => curr.regionInteractiveShare[d]).reduce((prev, curr) => prev + curr, 0);
		
				return curr.calculatedSeats[party] ? {
				party,
				votes:    prev.votes + (curr.turnout * (curr.regionInteractiveShare[party] / total)),
				fptp:     prev.fptp + curr.calculatedSeats[party].fptp,
				overhang: prev.overhang + curr.calculatedSeats[party].overhang,
				ave:      prev.ave + curr.calculatedSeats[party].ave,
				max:      prev.max + curr.calculatedSeats[party].max,
				} : prev;
			}, {
				votes:0,
				fptp:0,
				overhang:0,
				ave:0,
				max: 0,
			});
		
			values.votes = values.votes.toFixed(1);
		
			return values;
			});
		
			countryTotalRd2 = countryDistrubtionR2.reduce((prev, curr) => {
			return {
				votes:       prev.votes + +curr.votes,
				max:     prev.max + curr.max,
				overhang: prev.overhang + curr.overhang,
			};
			}, {
			votes:0,
			max:0,
			overhang:0,
			})
		
			rd2Result = calculatePrSeatsRd2({ total: countryTotalRd2.votes, 
				parties: partiesArr, 
				prevRound: countryDistrubtionR2});
		
			regionDistrubtionR3 = calculatePrSeatsRd3(rd2Result, $regionsData, partiesArr);
		
			countryTotal = countryDistrubtion.reduce((prev, curr) => {
			let max = Math.max(curr.max, curr.fptp);
			return {
				pr:       prev.pr + curr.pr,
				fptp:     prev.fptp + curr.fptp,
				overhang: prev.overhang + curr.overhang,
				ave:      prev.ave + curr.ave,
				max:      prev.max + max,
			};
			}, {
			pr:0,
			fptp:0,
			overhang:0,
			ave:0,
			max: 0
			})


	national = Object.values(regionDistrubtionR3).reduce((prev, curr) => {
		return {
			"CDU":  prev["CDU"] + curr["CDU"].finalResult,
			"SPD":  prev["SPD"] + curr["SPD"].finalResult,
			"AfD":  prev["AfD"] + curr["AfD"].finalResult,
			"FDP":  prev["FDP"] + curr["FDP"].finalResult,
			"LINKE": prev["LINKE"] + curr["LINKE"].finalResult,
			"Grn":  prev["Grn"] + curr["Grn"].finalResult,
		};
	}, {"CDU": 0,
		"SPD": 0,
		"AfD": 0,
		"FDP": 0,
		"LINKE": 0,
		"Grn": 0,
	});

	r3Total = Object.values(regionDistrubtionR3).reduce((prev, curr) => {
		return {
			newPr: prev.newPr + curr[selectedParty].newPr,
			prevMin: prev.prevMin + curr[selectedParty].prevMin,
			finalResult: prev.finalResult + curr[selectedParty].finalResult,
		}
	}, {
		newPr: 0,
		prevMin: 0,
		finalResult: 0,
	});

	regionDataMapped = $regionsData;
}
</script>
  
  <svelte:head>
	  <link href='https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap' rel='stylesheet' />
  </svelte:head>
  
<div class="sticky-controls">
	<div class="control-wrapper" class:showControls>
		<button on:click={() => showControls = !showControls} class="control-collapse">{showControls ? "<" : ">"}</button>
		<p>Pick a region: </p>
	<select bind:value={selectedRegion}>
		{#each regionData as d}
		<option value="{d.region}">{d.region}</option>
		{/each}
	</select>
	
	<p>Pick a party:</p>
	<select bind:value={selectedParty}>
		<option value="CDU">CDU</option>
		<option value="SPD">SPD</option>
		<option value="AfD">AfD</option>
		<option value="FDP">FDP</option>
		<option value="Grn">Grn</option>
		<option value="LINKE">Lnk</option>
	</select>
	
	<div>Change the {selectedParty} (national) swing:</div>
	<input type="range" min="-5" max="5" bind:value={selectedPartySwing}><span class="range-label">{selectedPartySwing}pp</span>
	
	<ul>
		<li class={stepA ? "sel" : ""}>Step A</li>
		<li class={stepB ? "sel" : ""}>Step B</li>
		<li class={stepC ? "sel" : ""}>Step C</li>
	</ul>
	
	<div class="mini-map">
		<Map showAll={stepB} {colorDictionary} {selectedYear} {selectedRegionData}  />
	</div>

	<p>Constituency results:</p>
	<button on:click={() => selectedYear = "17"} class={selectedYear === "17" ? "sel l" : "l"}>17</button><!--
	--><button on:click={() => selectedYear = "21"} class={selectedYear === "21" ? "sel r" : "r"}>21*</button>
	<p>* - INSA forecast</p>
	
</div>


</div>
		
<div class="preamble">
	<p>The Bundestag uses a three-part proportional representation distrbution system, using the <a href="https://en.wikipedia.org/wiki/Webster/Sainte-Lagu%C3%AB_method#Webster,_Sainte-Lagu%C3%AB,_and_Schepers" target="_new">Sainte-Laguë/Schepers</a> method to allocate seats proportionally.</p>

	<p>This happens at a regional level (step A) where the second votes are tallied and the allocation of all 599 seats is based on population.</p>

	<p>In step B, where the second votes are tallied nationally and the allocation of seats is compared to the seats allocated in step A. Seats are added until there isn't a disparity greater than three seats.</p>

	<p>Finally, in step C, these new numbers are allocated, party-by-party, at a regional level.</p>

	<p>Adapted from this <a href="https://www.bundeswahlleiter.de/en/dam/jcr/05c1185a-173f-4bab-80d6-51027c94b1bc/bwg2021_mustersitzberechnung_ergebnis2017.pdf" target="_blank">worked example</a>, this interactive calculator generates the PR list results based on either the 2017 constiuency results or the 2021 INSA forecast, plus the user changes to the party percent share of the second vote.</p>

	<p>Caveats:</p>
	<ul>
		<li>the first distribution of seats (based on voting population) is fixed</li>
		<li>this calculator only moves vote share around the fixed population/turnout numbers, it does not factor in turnout changes</li>
		<li>not tested <strike>thoroughly</strike> at all, ymmv</li>
		<li>it doesn't catch negative numbers</li>
		<li>probably rounding errors</li>
		<li>CDU includes CSU</li>
	</ul>
</div>


  
<div class="section">
	<Section on:intersecting={(ev) => stepA = ev.detail}>
		<h3>Step A</h3>
		<p>Firstly, calculate the party list seat distribution at a regional level.</p>

		<h5>{selectedParty}</h5>
		<table>
			<tr>
			<td>Region</td>
			<td>PR</td>
			<td>FPTP</td>
			<td>Overhang</td>
			<td>Avg</td>
			<td>Max</td>
			<td>Votes</td>
			</tr>
			{#each Object.entries($regionsData).slice(sliceStart, sliceEnd) as region}
			<tr class={region[1].region === selectedRegion ? "highlight": ""}>
			<td>{region[1].region}</td>
			<td class="num">{region[1].calculatedSeats[selectedParty].pr}</td>
			<td class="num">{region[1].calculatedSeats[selectedParty].fptp}</td>
			<td class="num">{region[1].calculatedSeats[selectedParty].overhang}</td>
			<td class="num">{region[1].calculatedSeats[selectedParty].ave}</td>
			<td class="num">{region[1].calculatedSeats[selectedParty].max}</td>
			<td class="num">{getVotes(region[1], selectedParty).toFixed(3)}</td>
			</tr>
			{/each}
			<tr class="clickable" on:click={() => showRows = !showRows}>
			<td>{showRows ? "^" :  "..."}</td>
			</tr>
			<tr>
			<td>Total</td>
			<td class="num">{totals.pr}</td>
			<td class="num">{totals.fptp}</td>
			<td class="num">{totals.overhang}</td>
			<td class="num">{totals.ave}</td>
			<td class="num">{totals.max}</td>
			<td class="num">{totals.votes.toFixed(3)}</td>
			</tr>
		</table>
	</Section>
</div>
  
<div class="section">
	<Section on:intersecting={(ev) => stepB = ev.detail}>
	<h3>Step B</h3>
	  <p>Then calculate the countrywide totals.</p>
	  
	  <h5>Countrywide</h5>
	  <table>
		<tr>
		  <td>Party</td>
		  <td>PR</td>
		  <td>FPTP</td>
		  <td>Overhang</td>
		  <td>Avg</td>
		  <td>Max</td>
		</tr>
		{#each countryDistrubtion as party}
		<tr class={party.party === selectedParty ? "highlight": ""}>
		  <td>{party.party}</td>
		  <td class="num">{party.pr}</td>
		  <td class="num">{party.fptp}</td>
		  <td class="num">{party.overhang}</td>
		  <td class="num">{party.ave}</td>
		  <td class="num">{party.max}</td>
		</tr>
		{/each}
		<tr>
		  <td>Total</td>
		  <td class="num">{countryTotal.pr}</td>
		  <td class="num">{countryTotal.fptp}</td>
		  <td class="num">{countryTotal.overhang}</td>
		  <td class="num">{countryTotal.ave}</td>
		  <td class="num">{countryTotal.max}</td>
		</tr>
	  </table>

	<p>Change the seat numbers until there is parity.</p>
	
	<h5>Countrywide</h5>
	<table>
		<tr>
		<td>Party</td>
		<td>Second votes (simplified)</td>
		<td>PR re-distributed</td>
		<td>Overhang</td>
		</tr>
		{#each countryDistrubtionR2 as party}
		{#if rd2Result && rd2Result[party.party]}
		<tr class={party.party === selectedParty ? "highlight": ""}>
			<td>{party.party}</td>
			<td class="num">{party.votes}</td>
			<td class="num">{rd2Result[party.party]}</td>
			<td class="num">{Math.max(0, party.max - rd2Result[party.party])}</td>
		</tr>
		{/if}
		{/each}
		<tr>
		<td>Total</td>
		<td class="num">{countryTotalRd2.votes}</td>
		<td class="num">{countryTotalRd2.max}</td>
		<td class="num">{countryTotalRd2.overhang}</td>
		</tr>
	</table>
	</Section>
</div>


<div class="section">
	<Section on:intersecting={(ev) => stepC = ev.detail}>
	<h3>Step C</h3>
  <p>Distribute these seats to the regions.</p>
  
  <h5>{selectedParty}</h5>
  <table>
	<tr>
	  <td>Region</td>
	  <td>Votes</td>
	  <td>New PR</td>
	  <td>Prev Min</td>
	  <td>Result</td>
	</tr>
	{#each Object.entries(regionDistrubtionR3).slice(sliceStart, sliceEnd) as region}
	<tr class={region[0] === selectedRegion ? "highlight": ""}>
	  <td>{region[0]}</td>
	  <td class="num">{region[1][selectedParty].share.toFixed(2)}</td>
	  <td class="num">{region[1][selectedParty].newPr}</td>
	  <td class="num">{region[1][selectedParty].prevMin}</td>
	  <td class="num">{region[1][selectedParty].finalResult}</td>
	</tr>
	{/each}
	<tr class="clickable" on:click={() => showRows = !showRows}>
	  <td>{showRows ? "^" :  "..."}</td>
	</tr>
	<tr>
	  <td>Total</td>
	  <td class="num"></td>
	  <td class="num">{r3Total.newPr}</td>
	  <td class="num">{r3Total.prevMin}</td>
	  <td class="num">{r3Total.finalResult}</td>
	</tr>
  </table>
</Section>
</div>
  
<div class="section">
	<h3>Final results:</h3>

	<div class="results-grid">
		<div class="full-map">
			<Map showAll={true} {colorDictionary} {selectedYear} {selectedRegionData} />
		</div>

		<div class="dot-grid">
		   {#each Object.keys(national) as party}
            {#each new Array(national[party]).fill(0) as d}
              <span class="cir fade" style="background: {colorDictionary[party]}"></span>
            {/each}
          {/each}
		</div>
	</div>

	<div>
		<table>
		  <tr>
			<td>Party</td>
			<td>FPTP</td>
			<td>PR</td>
			<td>Total</td>
		  </tr>
		  {#each countryDistrubtion as party, i}
		  <tr class={party.party === selectedParty ? "highlight": ""}>
			<td class={party.party}>{party.party}</td>
			<td class="num">{party.fptp}</td>			
			<td class="num">{rd2Result[party.party] - party.fptp}</td>
			<td class="num">{rd2Result[party.party]}</td>
		  </tr>
		  {/each}
		  <tr>
			<td>Total</td>
			<td class="num">{countryTotal.fptp}</td>
			<td class="num">{countryTotalRd2.max}</td>
			<td class="num">{countryTotal.fptp + countryTotalRd2.max}</td>
		  </tr>
		</table>
	</div>

	{#if selectedRegionData}
	  <Region region={selectedRegionData} {colorDictionary} calculated={regionDistrubtionR3} {selectedYear} />
	{/if}
	</div>
  
  
