import { writable } from 'svelte/store';
import { calculatePrSeats } from './calculatePr';

let mapRegionData = function(regionData, HexData, partyListResults, Pred21, use21) {
    let output = {};
    let j = 1;
    for(let i = 0; i < regionData.length; i++) {
      let startId = j;
      let endId = j + +regionData[i].constituency;

      let constitencyData = use21 ? Pred21 : HexData;
      let seats = constitencyData.filter(e => +e.id >= startId && +e.id < endId);

      let listSeats = {
          LINKE: +regionData[i].LINKE,
          SPD: +regionData[i].SPD,
          Grn: +regionData[i].GRN,
          FDP: +regionData[i].FDP,
          CDU: +regionData[i].CDU,
          AfD: +regionData[i].AfD,
      };
      let fptpSeats = {
          LINKE: 0,
          SPD: 0,
          Grn: 0,
          FDP: 0,
          CDU: 0,
          AfD: 0,
      };

      function changeSeatCount(party) {
        fptpSeats[party] += 1;

        if(listSeats[party] > 0) {
          listSeats[party] -= 1;
        }
      }
      
      for(let k = 0; k < seats.length; k++) {
        if(seats[k].party === "CDU" || seats[k].party === "CSU") {
          changeSeatCount("CDU");
        }
        if(seats[k].party === "SPD") {
          changeSeatCount("SPD");
        }
        if(seats[k].party === "FDP") {
          changeSeatCount("FDP");
        }
        if(seats[k].party === "LINKE") {
          changeSeatCount("LINKE");
        }
        if(seats[k].party === "AfD") {
          changeSeatCount("AfD");
        }
        if(seats[k].party === "GRÜNE") {
          changeSeatCount("Grn");
        }
      }

      let partyListResult = {...partyListResults.find(e => e.region === regionData[i].region)};
      let pr = +partyListResult["pr"];
      let turnout = +partyListResult["turnout"];
      delete partyListResult["turnout"];
      delete partyListResult["region"];
      delete partyListResult["pr"];
      delete partyListResult["Oth"];

      let keys = Object.keys(partyListResult);
      for(let i = 0; i < keys.length; i++) {
        partyListResult[keys[i]] = + partyListResult[keys[i]] 
      }

      let d = {
        region: regionData[i].region,
        turnout,
        startId,
        endId,
        seats,
        listSeats,
        listTotal: +regionData[i].total - seats.length,
        fptpSeats,
        fptpTotal: seats.length,
        total: +regionData[i].total,
        pr,
        partyListResult,
        data: regionData[i],
        name: regionData[i].region,
        wardSeats: regionData[i].fptpTotal,
        wardResults: seats,
        calculatedSeats: [],  
        regionVoteShare: partyListResult,
        wardArray: [],
        wardData: [],
        regionInteractiveShare: {...partyListResult},
      };

      d.calculatedSeats = calculatePrSeats(d);

      output[regionData[i].region] = d;

      j += +regionData[i].constituency
    }
    return output;
  }

function createRegions() {
	const { subscribe, set, update } = writable(0);

	return {
        init: (regionData, HexData, partyListResults, Pred21, use21) => set(mapRegionData(regionData, HexData, partyListResults, Pred21, use21)),
        set,
		subscribe,
	};
}

export const regionsData = createRegions(0);